.modal {
  min-width: 350px;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  z-index: 2000;
   @apply absolute bg-white p-4 shadow-lg rounded;
}
 .modal > .modal-head {
  @apply relative;
}
 .modal-wrapper::after {
  content: "";
